<template>
  <div
    class="box"
    :class="{'chess-active': piece.active, 'green-5': (colIndex + rowIndex) % 2 == 0, 'green-1': (colIndex + rowIndex) % 2 != 0}"
  >
    <div class="box-content">
      <span v-if="piece.available" class="chess-available"></span>
      <i v-if="piece" :style="{color: piece.color}" :class="'fas fa-chess-'+piece.icon"></i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    piece: { type: Object, required: true },
    colIndex: { type: Number, required: true },
    rowIndex: { type: Number, required: true },
  },
};
</script>