<template>
  <div class="text-center">
    <v-dialog v-model="dialog" hide-overlay :persistent="!notFound" width="500">
      <v-card>
        <v-card-title v-if="notFound">Not found. Please try again after sometime.</v-card-title>

        <v-card-title v-else>
          Searching player. Please stand by
          <v-progress-linear indeterminate color="white" class="my-2"></v-progress-linear>
        </v-card-title>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: true,
      notFound: false,
    };
  },

  created() {
    setTimeout(() => {
      this.notFound = true;
      this.$emit('notFound');
    }, 15000);
  },
};
</script>
