// import { WHITE, WHITE_PAWNS, BLACK, BLACK_PAWNS, AVAILABLE } from './chess.constant';
import { first, cloneDeep } from 'lodash';
import Swal from 'sweetalert2';
import { DatabaseService } from '@/services';
import HanoiService from './hanoi.service';

export default {
  endCountdown() {
    const [player1, player2] = this.players;

    let winner = player1;
    if (player1.level < player2.level) {
      winner = player2;
    }

    winner.amount += 1000;

    DatabaseService.updateById(winner.id, {
      amount: winner.amount,
    });

    HanoiService.setRoom(this.$route.params.id, winner.id, {
      amount: winner.amount,
    });

    // this.alert({
    //   title: winner.name,
    // });
  },
  createRoom() {
    HanoiService.createRoom(
      'hanoi',
      {
        id: this.user.id,
        level: 1,
      },
      Date.now()
    );
  },
  getPlayers() {
    HanoiService.setRoom(this.$route.params.id, this.user.id, {
      id: this.user.id,
      name: this.user.name,
      amount: this.user.amount,
      picture: this.user.picture,
      level: this.user.level || 1,
    });

    HanoiService.getRoom(this.$route.params.id, (users1) => {
      this.players = Object.values(users1);
      if (this.players.length === 2) {
        this.loader = false;
      }
    });
  },
  setBalls() {
    const colors = [
      'red',
      'pink',
      'purple',
      'blue',
      'green',
      'yellow',
      'orange',
      'brown',
    ]; // 'maroon', 'purple', 'grey', 'lightgreen', 'navy'

    colors.forEach((color) => {
      for (let index = 0; index < 4; index++) {
        this.balls.push(color);
      }
    });
  },

  alert(option) {
    return Swal.fire({
      confirmButtonColor: '#668fff',
      customClass: {
        title: 'title',
        confirmButton: 'btn',
      },
      ...option,
    });
  },

  undo() {
    if (this.history.length) {
      this.undoCount--;

      const [from, to, count] = this.history.pop();
      const column = this.rows[from];
      const activeColumn = this.rows[to];

      const balls = activeColumn.splice(0, count);
      column.unshift(...balls);
    }
  },
  info() {
    this.alert({
      title: 'How to play',
      html: ` <ul>
                        <li>Tap any tower to move the ball lie on top in the tower to another tower.</li><br />
                        <li>The rule is that you can only move ball(s) on top of another ball if both of them have the same color and the tower you want to move into has enough space.</li><br />
                        <li>Try not to get stuck - but dont worry, you can always restart the level at any time.</li><br />
                    </ul>`,
    });
  },
  retry() {
    this.history = [];
    this.activeBall = 1;
    this.activeColumn = null;
    this.rows = cloneDeep(this.copyRows);
  },
  levelCompleted() {
    return this.rows.every((column) => {
      return (
        (column.length == 0 || column.length == 4) &&
        column.every((ball) => {
          return ball == column[0];
        })
      );
    });
  },
  nextLevel() {
    this.level++;

    HanoiService.setRoom(this.$route.params.id, this.user.id, {
      level: this.level,
    });

    this.undoCount = 5;
    this.setRows();
  },
  setRows() {
    this.rows = [];
    this.history = [];
    this.activeBall = 1;
    this.activeColumn = null;

    const balls = cloneDeep(this.balls);

    let row = [];
    while (balls.length) {
      const number = Math.round(Math.random() * balls.length);

      if (balls[number]) {
        const [ball] = balls.splice(number, 1);
        row.push(ball);

        if (row.length == 4) {
          this.rows.push(row);
          row = [];
        }
      }
    }

    this.rows.push([], []);
    this.copyRows = cloneDeep(this.rows);
    this.$store.set('hanoi/rows', this.rows);
  },
  onKeydown(event) {
    // console.log(event);

    if (event.ctrlKey) {
      if (event.key == 'z') {
        this.undo();
      }
    }

    if (event.key == 'F1') {
      event.preventDefault();
      this.info();
    }

    if (event.which == 32) {
      // space
      if (this.activeColumn || this.activeColumn == 0) {
        this.onColumnClick(this.cursor);
      } else {
        this.activeColumn = this.cursor;
      }
    } else if (event.which == 39) {
      // right
      if (this.cursor < this.rows.length - 1) {
        this.cursor++;
      } else {
        this.cursor = 0;
      }
    } else if (event.which == 37) {
      // left
      if (this.cursor > 0) {
        this.cursor--;
      } else {
        this.cursor = this.rows.length - 1;
      }
    }
  },
  onColumnClick(index) {
    this.cursor = index;
    const column = this.rows[index];

    if (this.activeColumn == index) {
      if (column[this.activeBall] == column[this.activeBall - 1]) {
        this.activeBall++;
      } else {
        this.activeBall = 1;
        this.activeColumn = null;
      }
    } else {
      const activeColumn = this.rows[this.activeColumn];

      if (this.activeColumn || this.activeColumn == 0) {
        if (column.length < 4 && 4 - column.length >= this.activeBall) {
          if (!first(column) || first(column) == first(activeColumn)) {
            const balls = activeColumn.splice(0, this.activeBall);
            column.unshift(...balls);

            if (this.undoCount) {
              this.history.push([this.activeColumn, index, this.activeBall]);
            }

            if (this.levelCompleted()) {
              this.alert({
                title: 'Whoopee',
                confirmButtonText: 'Next',
              }).then((result) => {
                if (result.value) {
                  this.nextLevel();
                }
              });
            }

            this.activeBall = 1;
            this.activeColumn = null;
            return;
          }
        }
      }

      if (column && column.length) {
        this.activeBall = 1;
        this.activeColumn = index;
      }
    }
  },
};
