import firebase from 'firebase';

const database = firebase.database();

export default {
  getRoom(id, callback) {
    database.ref(`hanoi/${id}`).on('value', function(snapshot) {
      callback(snapshot.val());
    });
  },

  setRoom(id, userId, user) {
    database.ref(`hanoi/${id}/${userId}`).update(user);
  },
};
