import { sync } from 'vuex-pathify';
import VueCountdown from '@chenfengyuan/vue-countdown';

import methods from './hanoi.methods';

import AppBar from '@/components/app-bar';
import Loader from '@/components/loader';
import player from './components/player.component';
import Tower from './components/tower.component';

export default {
  name: 'hanoi',
  components: {
    VueCountdown,
    AppBar,
    Loader,
    Tower,
    player,
  },
  data() {
    return {
      xs: false,
      loader: false,
      activeBall: 1,
      activeColumn: null,
      cursor: null,
      history: [],
      time: 60,
      players: [],
      users: [],
      balls: [],
      l: 'medium',
      levels: ['easy', 'medium', 'hard'],
    };
  },
  computed: {
    ...sync(['hanoi/rows', 'hanoi/level', 'hanoi/undoCount']),
    width() {
      return Math.floor(screen.width / 8);
    },
  },
  created() {
    this.setBalls();
    if (!this.rows.length) {
      this.setRows();
    }
    // this.getPlayers();
  },
  methods: methods,
};
